import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Avaliacao_ideia } from '../../../modelo/Avaliacao_ideia'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'avaliacao_ideia-dados-edit',   templateUrl: 'avaliacao_ideia_dadosEdit.component.html' }) export class Avaliacao_ideiaDadosEditComponent implements OnInit {    avaliacao_ideia: Avaliacao_ideia;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.avaliacao_ideia.Avaliacao_ideia";                 let id = +params['id'];         this.avaliacao_ideia = new Avaliacao_ideia();         this.avaliacao_ideia.cd_avaliacao_ideia = id;         o.objetoJson = this.avaliacao_ideia;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a avaliacao_ideia. ';       }      this.avaliacao_ideia = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


